@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Section1{
    background: #DA2929;
    color:#ffffff;
    height:  calc(100vh - 75px);
}
.Section1Pokeball{
    position: absolute;
    top: 30vh;
    left: -10vw;
    -webkit-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
}
.Pikachu{
    position: absolute;
    right:5vw;bottom:0;
}
.Bulbasor{
    position: absolute;
    right:35vw;bottom:0vh;
}
.Charmander{
    position: absolute;
    right:45vw;bottom:30vh;

}
.Pokemon1{
    position: absolute;
    right:65vw;bottom:60vh;

}
.Section1Title{
    position: absolute;
    top:calc(75px + 12vh);
    right:10vw;
    width:30vw;
}
.Section1TitleT1{
    font-weight: bold;
    font-size: 72px;
    line-height: 84px;
    text-transform: capitalize;
    color: #FFFFFF;
    
}
.Section1TitleT1B1{
    
    font-weight: bold;
    font-size: 72px;
    line-height: 84px;
    text-transform: capitalize;
    color: #FFD63E;
    
}
.Section2{
}
.section2-row{
    height: 100%;
    display: grid;
    grid-template-columns: 50% 50% ;
}
.section2-row > div{
    text-align: center;
}
.section2-left{
    text-align: center;
    width: 100%;
}
.section2-right{
    position: relative;
    top:20vh;
    width: 100%;
}
.section2-right > .section2-title{
font-weight: bold;
font-size: 96px;
line-height: 100px;
letter-spacing: -5px;
color: #000000;

}
.section2-left-container{
    position: relative;
    right: 0;
    top:40vh;
    display: grid;
    place-content: center;
    
}
.section2-left-container > div{
    width: 512px;
}
.eachResultContainer{
    border: 1px solid #dedede;
    box-shadow: 0px 0px 10px #dedede;
    padding:10px;
}
.Navbar{
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    border-bottom: 1px solid #dedede;
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.NavbarItem{
    height: 100%;
    display: grid;
    place-content: center;
    transition-duration: 0.2s;
    border-top: 1px solid #ffffffff;
}
.NavbarItem:hover{
    border-top: 1px solid black;
}
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.user-select-none{
    
  -webkit-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
